import React, { useState } from "react";
import { toast } from "react-toastify";
import axios from "axios";
import News from "./News";
const Employ = () => {
  const [formData, setFormData] = useState({});
  let headersList = {
    Accept: "/",
    "Content-Type": "multipart/form-data"
  };
  let requestOptions = {
    url: "https://el-twyan.onrender.com/api/v1/apply",
    method: "POST",
    headers: headersList,
    data: formData
  };
  const handleSubmit = e => {
    e.preventDefault();
    axios
      .request(requestOptions)
      .then(() => {
        document.getElementById("apply_form").reset();
        toast.success("    ");
      })
      .catch(err => {
        console.error(err);
        document.getElementById("apply_form").reset();
        toast.error("  ");
      });
  };
  return (
    <React.Fragment>
      <header className="employ_header">
        <div className="image" />
        <div className="path_desc">
          <h2>
            <span>/</span>        
                 .
          </h2>
        </div>
      </header>
      <section className="employ_form">
        <h3>  </h3>
        <form id="apply_form" onSubmit={handleSubmit}>
          <input
            type="text"
            name="name"
            placeholder=" "
            required
            onChange={e =>
              setFormData({ ...formData, fullName: e.target.value })}
          />
          <input
            required
            type="email"
            name="email"
            placeholder=" "
            onChange={e => setFormData({ ...formData, email: e.target.value })}
          />
          <input
            type="tel"
            name="phone"
            placeholder=" "
            required
            onChange={e => setFormData({ ...formData, phone: e.target.value })}
          />
          <div className="file_feild">
            <p>  </p>
            <input
              required
              type="file"
              name="file"
              className="file_in"
              placeholder="  "
              accept="application/pdf, application/vnd.openxmlformats-officedocument.wordprocessingml.document"
              onChange={e => {
                console.log(e.target.files[0]);
                setFormData({ ...formData, resume: e.target.files[0] });
              }}
            />
          </div>
          <textarea
            name="message"
            placeholder=""
            required
            onChange={e =>
              setFormData({ ...formData, message: e.target.value })}
          />
          <button type="submit"></button>
        </form>
      </section>
      <News />
    </React.Fragment>
  );
};

export default Employ;
